"use client";

import React, { useCallback, useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import type { ReactNode } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Skeleton } from "@/components/ui/skeleton";
import { useToast } from "@/components/ui/toast";

const Detail: React.FC<{
  label: string;
  value: ReactNode;
  description?: ReactNode;
  className?: string;
}> = ({ label, value, description, className }) => (
  <div
    className={`space-y-1 rounded border border-border/60 p-3 ${
      className || ""
    }`}
  >
    <div className="text-xs uppercase tracking-wide text-muted-foreground">
      {label}
    </div>
    <div className="text-sm text-gray-900 font-medium break-words">{value}</div>
    {description && (
      <div className="text-xs text-muted-foreground">{description}</div>
    )}
  </div>
);

export default function SupplierKycPage() {
  const router = useRouter();
  const { show } = useToast();
  const [loading, setLoading] = useState(true);
  const [user, setUser] = useState<any>(null);
  const [kyc, setKyc] = useState<any | null>(null);
  const [refreshing, setRefreshing] = useState(false);
  const [editing, setEditing] = useState(false);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [pendingForm, setPendingForm] = useState({
    supplier_name: "",
    email: "",
    phone: "",
    address: "",
    national_id: "",
    tin_number: "",
    license_number: "",
    notes: "",
  });

  const loadKyc = useCallback(async () => {
    try {
      // Mock KYC data - replace with actual API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const mockKyc = {
        supplier_name: "MediSupply Global",
        email: "contact@medisupply.com",
        phone: "+1-555-0123",
        address: "123 Medical District, New York, USA",
        national_id: "ID123456789",
        tin_number: "TIN987654321",
        license_number: "SUP-2024-001",
        notes: "Specialized in pharmaceutical distribution with 20+ years experience.",
        status: "pending"
      };
      
      setKyc(mockKyc);
      setPendingForm({
        supplier_name: mockKyc.supplier_name || "",
        email: mockKyc.email || "",
        phone: mockKyc.phone || "",
        address: mockKyc.address || "",
        national_id: mockKyc.national_id || "",
        tin_number: mockKyc.tin_number || "",
        license_number: mockKyc.license_number || "",
        notes: mockKyc.notes || "",
      });
    } catch (err: any) {
      show({
        variant: "destructive",
        title: "Unable to load application",
        description: err?.message || "Unknown error",
      });
    }
  }, [show]);

  useEffect(() => {
    let active = true;
    (async () => {
      setLoading(true);
      try {
        // Mock user data - replace with actual API call
        await new Promise(resolve => setTimeout(resolve, 500));
        
        const mockUser = {
          id: 1,
          email: "supplier@company.com",
          role: "supplier",
          kyc_status: "pending"
        };
        
        if (!active) return;
        setUser(mockUser);
        
        const kycApproved = mockUser?.kyc_status === "approved";
        
        if (kycApproved) {
          router.replace("/dashboard/supplier");
          return;
        }

        await loadKyc();
        setError(null);
      } catch (err: any) {
        if (!active) return;
        setError(err?.message || "Failed to load dashboard");
      } finally {
        if (active) setLoading(false);
      }
    })();
    return () => {
      active = false;
    };
  }, [router, loadKyc]);

  const handleFieldChange = (key: keyof typeof pendingForm, value: string) => {
    setPendingForm((prev) => ({ ...prev, [key]: value }));
  };

  const handleSave = async (e: React.FormEvent) => {
    e.preventDefault();
    setSaving(true);
    try {
      // Mock API call - replace with actual API
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      show({
        variant: "success",
        title: "Application updated",
        description: "We will review your changes shortly.",
      });
      setEditing(false);
      await loadKyc();
    } catch (err: any) {
      show({
        variant: "destructive",
        title: "Update failed",
        description: err?.message || "Unable to update application",
      });
    } finally {
      setSaving(false);
    }
  };

  const handleRefreshStatus = async () => {
    setRefreshing(true);
    try {
      // Mock refresh - replace with actual API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      show({
        variant: "success",
        title: "Status refreshed",
        description: "We have the latest update.",
      });
    } catch (err: any) {
      show({
        variant: "destructive",
        title: "Refresh failed",
        description: err?.message || "Unable to refresh status",
      });
    } finally {
      setRefreshing(false);
    }
  };

  if (loading) return <Skeleton className="h-64" />;
  if (error) return <div className="text-red-600 text-sm">{error}</div>;

  const supplierStatus = user?.kyc_status || "pending";

  return (
    <div className="max-w-4xl mx-auto px-4 py-8">
      {/* Header Section */}
      <div className="text-center mb-12">
        <div className="inline-flex items-center justify-center w-16 h-16 bg-green-100 rounded-full mb-4">
          <svg
            className="w-8 h-8 text-green-600"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
            />
          </svg>
        </div>
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Supplier Application Under Review
        </h1>
        <p className="text-gray-600 max-w-2xl mx-auto">
          Thank you for registering as a supplier. Our team will review your
          information and verify your credentials within 24-48 hours.
        </p>
        {supplierStatus === "rejected" && (
          <div className="mt-4 p-4 bg-red-50 border border-red-200 rounded-lg text-red-700 max-w-2xl mx-auto">
            ⚠️ Previous submission was rejected. Please review and update your
            details below.
          </div>
        )}
      </div>

      {/* Status Card */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 mb-8">
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center space-x-3">
            <div
              className={`w-3 h-3 rounded-full ${
                supplierStatus === "approved"
                  ? "bg-green-500"
                  : supplierStatus === "rejected"
                  ? "bg-red-500"
                  : "bg-yellow-500"
              }`}
            ></div>
            <div>
              <p className="text-sm text-gray-500 uppercase tracking-wide">
                Verification Status
              </p>
              <p className="text-xl font-semibold capitalize text-gray-900">
                {supplierStatus.replace("_", " ")}
              </p>
            </div>
          </div>
          <Button
            variant="outline"
            onClick={handleRefreshStatus}
            disabled={refreshing}
            className="flex items-center space-x-2"
          >
            <svg
              className={`w-4 h-4 ${refreshing ? "animate-spin" : ""}`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
              />
            </svg>
            <span>{refreshing ? "Refreshing..." : "Refresh"}</span>
          </Button>
        </div>

        {editing ? (
          <form onSubmit={handleSave} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="text-sm text-muted-foreground">
                  Supplier/Company Name
                </label>
                <Input
                  className="text-black"
                  value={pendingForm.supplier_name}
                  onChange={(e) =>
                    handleFieldChange("supplier_name", e.target.value)
                  }
                  required
                />
              </div>
              <div>
                <label className="text-sm text-muted-foreground">
                  Email Address
                </label>
                <Input
                  type="email"
                  value={pendingForm.email}
                  onChange={(e) =>
                    handleFieldChange("email", e.target.value)
                  }
                  className="text-black"
                  required
                />
              </div>
              <div>
                <label className="text-sm text-muted-foreground">
                  Phone Number
                </label>
                <Input
                  value={pendingForm.phone}
                  onChange={(e) =>
                    handleFieldChange("phone", e.target.value)
                  }
                  placeholder="+1-555-..."
                  className="text-black"
                />
              </div>
              <div>
                <label className="text-sm text-muted-foreground">
                  Business Address
                </label>
                <Input
                  value={pendingForm.address}
                  onChange={(e) =>
                    handleFieldChange("address", e.target.value)
                  }
                  className="text-black"
                />
              </div>
              <div>
                <label className="text-sm text-muted-foreground">
                  National ID Number
                </label>
                <Input
                  value={pendingForm.national_id}
                  onChange={(e) =>
                    handleFieldChange("national_id", e.target.value)
                  }
                  required
                  className="text-black"
                />
              </div>
              <div>
                <label className="text-sm text-muted-foreground">
                  TIN Number
                </label>
                <Input
                  value={pendingForm.tin_number}
                  onChange={(e) =>
                    handleFieldChange("tin_number", e.target.value)
                  }
                  required
                  className="text-black"
                />
              </div>
              <div className="md:col-span-2">
                <label className="text-sm text-muted-foreground">
                  Supplier License Number
                </label>
                <Input
                  value={pendingForm.license_number}
                  onChange={(e) =>
                    handleFieldChange("license_number", e.target.value)
                  }
                  required
                  className="text-black"
                />
              </div>
              <div className="md:col-span-2">
                <label className="text-sm text-muted-foreground">
                  Additional Notes
                </label>
                <textarea
                  className="mt-1 w-full rounded border border-input px-3 py-2 text-sm shadow-sm focus-visible:outline-none focus-visible:ring focus-visible:ring-ring text-black"
                  rows={4}
                  value={pendingForm.notes}
                  onChange={(e) => handleFieldChange("notes", e.target.value)}
                  placeholder="Any additional information about your business..."
                />
              </div>
            </div>
            <div className="flex flex-wrap gap-3">
              <Button type="submit" disabled={saving} className="bg-green-600 hover:bg-green-700">
                {saving ? "Saving..." : "Submit Updates"}
              </Button>
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setEditing(false);
                  if (kyc) {
                    setPendingForm({
                      supplier_name: kyc?.supplier_name || "",
                      email: kyc?.email || "",
                      phone: kyc?.phone || "",
                      address: kyc?.address || "",
                      national_id: kyc?.national_id || "",
                      tin_number: kyc?.tin_number || "",
                      license_number: kyc?.license_number || "",
                      notes: kyc?.notes || "",
                    });
                  }
                }}
                disabled={saving}
              >
                Cancel
              </Button>
            </div>
          </form>
        ) : (
          <div className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
              <Detail label="Supplier/Company Name" value={kyc?.supplier_name || "—"} />
              <Detail label="Email Address" value={kyc?.email || "—"} />
              <Detail label="Phone Number" value={kyc?.phone || "—"} />
              <Detail label="Business Address" value={kyc?.address || "—"} />
              <Detail label="National ID Number" value={kyc?.national_id || "—"} />
              <Detail label="TIN Number" value={kyc?.tin_number || "—"} />
              <Detail label="License Number" value={kyc?.license_number || "—"} />
              <Detail label="Additional Notes" value={kyc?.notes || "No additional notes"} />
            </div>
            <div className="flex flex-wrap gap-3">
              <Button
                onClick={() => setEditing(true)}
                className="bg-green-600 hover:bg-green-700"
              >
                Edit Submission
              </Button>
            </div>
          </div>
        )}
      </div>

      {/* Information Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
          <div className="flex items-start space-x-3">
            <div className="flex-shrink-0">
              <svg className="w-5 h-5 text-blue-600 mt-0.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <h3 className="font-medium text-blue-900 mb-1">What happens next?</h3>
              <p className="text-sm text-blue-700">
                Our verification team will review your supplier credentials, business licenses, and contact information. You'll receive an email notification once the review is complete.
              </p>
            </div>
          </div>
        </div>

        <div className="bg-green-50 border border-green-200 rounded-lg p-4">
          <div className="flex items-start space-x-3">
            <div className="flex-shrink-0">
              <svg className="w-5 h-5 text-green-600 mt-0.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <h3 className="font-medium text-green-900 mb-1">After Approval</h3>
              <p className="text-sm text-green-700">
                Once approved, you'll gain access to your full supplier dashboard where you can manage products, process orders, and connect with pharmacy partners.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="rounded border border-amber-200 bg-amber-50 p-4 text-sm text-amber-800">
        <div className="flex items-start space-x-2">
          <svg className="w-4 h-4 text-amber-600 mt-0.5 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
          </svg>
          <div>
            <p className="font-medium mb-1">Important Notice</p>
            <p>
              You can continue to log in and monitor this page. Once approved, you'll have full access to the supplier marketplace and can start connecting with verified pharmacies in our network.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}